<template lang="pug">
div
  .content-wrapper
   section
    .container
     .auth
      .auth__banner
        h1.auth__title Hello Login
      .auth__form
         h1.auth__title Sign In
         form.form-group(@submit.prevent="submit")
          .form-item(:class="{ errorInput: $v.email.$error }")
            input(
            type="email" 
            placeholder="Email"
            v-model="email"
            :class="{ error: $v.email.$error }"
            @change="$v.email.$touch()"

            )
            .error(v-if="!$v.email.required") Field is required
            .error(v-if="!$v.email.email") Email is not correct
          .form-item(:class="{ errorInput: $v.password.$error }")
            input(
            type="password" 
            placeholder="Password"
            v-model="password"
            :class="{ error: $v.password.$error}"
            @change="$v.password.$touch()"

            )
            .error(v-if="!$v.password.required") Password is required.
            .error(v-if="!$v.password.minLength")
                  | Password must have at least {{ $v.password.$params.minLength.min }} letters.    

           
          button.button__submit(type="submit" :disabled="submitStatus === 'PENDING'") Sing In
          .button__list
           span Do you have register?
            router-link(to="/registration")  Enter here
 
          p.typo__p(v-if="submitStatus === 'OK'") Thanks for your submission!
          p.typo__p(v-if="submitStatus === 'ERROR'") Please fill the form correctly.
          p.typo__p(v-if="submitStatus === 'PENDING'") Sending...
    
         
      
      
    

</template>


<script>

import { required , email , minLength , maxLength  } from 'vuelidate/lib/validators'

export default {
    data () {
        return {
           
            email: '',
            password: '',
            repeatPassword: '',
            submitStatus: null
        }
    },
     validations: {
        email: {
          required ,
          email    
          
        } , 
        password: {
            required ,
            minLength: minLength(4)
        } 
         
  } ,
    methods: {
    submit() {
      console.log('submit!')
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.submitStatus = 'ERROR'
      } else {
        // do your submit logic here
       
        const user = {
            email: this.email,
            password: this.password
        } 
        console.log(user);  
        this.submitStatus = 'PENDING'
        setTimeout(() => {
          this.submitStatus = 'OK'
        }, 500)
      }
    }
  }    
}
</script>


<style lang="stylus">




.auth__title

.auth
 display -webkit-flex
 display -moz-flex
 display -ms-flex
 display -o-flex
 display flex
 flex-direction row
 width: 100%
 flex-direction row
 width 90%
 justify-content space-around
 margin 25px auto



.form input
 display block
 margin 15px 0
 border-radius 5px
 border 1px solid #000
 padding 5px 15px
 &input
  font-size: 16px



input 
 &.error 
  border 1px solid #fc5c65
 &.active 
   border 1px solid green
 
.button__submit
 background green
 color #fff
 border none
 padding 3px 15px
 border-radius 5px
 opacity .7
 transition 500ms
 margin 5px 0


 &:hover
  opacity: 1











</style>


