export default {
    state: {
        // обьект с тасками
      tasks: [
        {
            'id':1,
            'title': 'GrowthBusters: Hooked on Growth',
            'description': 'I directed this movie',
            'whatWatch': 'Film',
            'completed': false,
            'editing': false
        },
         {
            'id':2,
            'title': 'Game of thrones',
            'description': 'Best serial ',
            'whatWatch': 'Serial',
            'completed': false,
            'editing': false
        },
        ]
    },
    mutations: {
        newTask (state , payload) {
            state.tasks.push(payload)
        }
    },
    actions: {
        newTask({commit} , payload) {
            // генерируем случайный id
            payload.id = Math.random()
            // выполняем мутация асинхронно
            commit('newTask' , payload)
        }
    } ,
    getters: {
        // возвращаем массив тасков
        tasks(state) {
            return state.tasks
        } ,
        taskCompleted (state) {
            return state.tasks.filter(task => {
                return task.completed
            })
        } ,
        taskNotCompleted (state) {
            return state.tasks.filter(task => {
                return task.completed === false
            })
        }
        
    }
}


