<template lang="pug">
div
  .content-wrapper
   section
    .container
     h1 Home
     input(
      type="text"
      placeholder="What we will watch?"
      v-model="taskTitle"
      @keyup.enter="newTask"
     )
     br
     br
     textarea(
      type="textarea"
     
      v-model="taskDescription"
      @keyup.enter="newTask"
     )
     .option-list
      input.what-watch__radio( type="radio" , value="Film" , id="radioFilm" , v-model="taskWhatWatch" @keyup.enter="newTask")
      label(
        for="radioFilm"
      ) Film 
       input.what-watch__radio( type="radio" , value="Serial" , id="radioSerial" , v-model="taskWhatWatch" @keyup.enter="newTask")
      label(
        for="radioSerial"
      ) Serial
       // Total film
     .total-time
      .total-time__film(
      v-if="taskWhatWatch === 'Film'"
      )
       span.time-title Hours
       input.time-input(
         type="number"
        v-model="filmHours"
       )
       
       span.time-title Minutes
       input.time-input(
        type="number"
        v-model="filmMinutes"
       )
       p {{ filmTime }}
       // Total serial
      .total-time__serial(
      v-if="taskWhatWatch === 'Serial'"
      )
       span.time-title How many season ?
       input.time-input(
        type="number"
        v-model="serialSeason"
       )
       span.time-title How many series ?
       input.time-input(
        type="number"
        v-model="serialSeries"
       )
       span.time-title How long is one series ?(minutes) 
       input.time-input(
        type="number"
        v-model="serialMinutes"
       )
       p {{ serialTime }}
       // TAG LIST
       // Add new Tag
      .tag-list.tag-list--add
       .tag-wrapper(
       
       )
        span.tag Add new
        span.tag {{ tagMenuShow }}
        span.button-close(
          @click="tagMenuShow = !tagMenuShow"
         :class="{active : !tagMenuShow}"
        )
        // Show input
        .tag-list.tag-list--menu(
        v-if="tagMenuShow")
         input.tag-add-input(
          type="text"
          placeholder="New tag"
          v-model="tagTitle"
          @keyup.enter="newTag"
          
         )
         
         .button.button-default(
          @click="newTag"
         ) Send
         // All tags
        .tag-list
         .tag__wrapper(
          v-for="tag in tags"
          :key="tag.title"
         )

          .tag(
           @click="addTagUsed(tag)"
           :class="{used: tag.use}"
          )
            span.tag-title {{ tag.title }}
            span.button-close(@click="deleteTag(tag.id)")
        p {{ tagsUsed }}     
         

      
   section
    .container
     
        
        
       

</template>


<script>

export default {
    data () {
        return {
            taskTitle: '',
            taskDescription: '',
      
            taskWhatWatch : '',
            // Total time film
            filmMinutes: 30,
            filmHours: 1,
            
            // Total time serial 
            serialSeason: 1,
            serialSeries: 11,
            serialMinutes: 40,
           
            /// Tags
            tagTitle: '',
            tagMenuShow: true,
            tagsUsed: [],
          
          
        }
    },
    methods: {
        
       newTag() {
            
        
         if(this.tagTitle === '' ) {
             return
         }
           
           const tag = {
               title: this.tagTitle,
               use: false
           }
           
           this.$store.dispatch('newTag' , tag)
           

            this.tags.push ({
                title: this.tagTitle,
                used: false
            })
            
            console.log()
        
        
    } ,    /// TASK ////
     newTask() {
         // заглушка отправки
         if(this.taskTitle === '' ) {
             return true
         }
         
         let time;
         if(this.whatWatch === 'Film')
            time = this.filmTime
         else
            time = this.serialTime; 
        
      
         
        const task = {
            
            title: this.taskTitle ,
            description: this.taskDescription ,
            whatWatch: this.taskWhatWatch,
            time,
            tags: this.tagsUsed,
            completed: false,
            editing: false 
             
         }
        // пушим данные в vuex
        // меняем данные в store через мутацию
        this.$store.dispatch('newTask' , task)
        
      
        
        console.log(task);
           
         
          this.taskTitle = '';
          this.taskDescription = '';
          this.tagsUsed = [];
         
         for(let i = 0;i < this.tags.length;i++) {
            this. tags[i].use = false;
         }
     }
      ,
        
       addTagUsed (tag) {
           tag.use = !tag.use
           if(tag.use) {
               this.tagsUsed.push({
                    title: tag.title
               })
              
           } 
            else {
                this.tagsUsed.splice(tag.title , 1)
                
            }
        } ,    
        
     getHoursAndMinutes (minutes) {
        let hours = Math.trunc(minutes / 60);
        let min = minutes % 60;
         
        return hours + ' : Hours , ' + min +' : Minutes ';
         
     }  
         
   } , 
    
    computed: {
        
        tags() {
           return  this.$store.getters.tags
        },
        
        // вычисление времени фильма
        filmTime () {
            let min  = (this.filmHours * 60) + (this.filmMinutes * 1);
            
            return this.getHoursAndMinutes(min);
        } ,
        
           // вычисление времени сериала
           // вычисление времени сериала
         serialTime () {
            let min  =  this.serialSeason * this.serialSeries * this.serialMinutes;
             return this.getHoursAndMinutes(min);
        } ,     
    }
}

</script>


<style lang="stylus" scoped>

 
.mycard__description
 display -webkit-flex
 display -moz-flex
 display -ms-flex
 display -o-flex
 display flex
 flex-direction column


.option-list .input.what-watch__radio
 margin 5px







.mycard
 margin 5px
 display -webkit-flex
 display -moz-flex
 display -ms-flex
 display -o-flex
 display flex
 justify-content space-between
 flex-direction row
 padding 15px

.tag
 margin 5px

 display inline-block
 padding 5px 20px
 text-align center
 background #fff
 color #424242
 border 1px solid #424242
 border-radius 5px

.time-title
 display block
 margin 10px 0


.tag-add-input
 display block
 margin 10px 0

 
//
// Tags
//
.tag-list
  margin-bottom 20px
.tag__wrapper
  margin-right 18px
  margin-bottom 10px
  &:last-child
    margin-right 0
.tag

  cursor pointer
  &.used
    background-color: #444ce0
    color #fff
    .button-close
      &:before,
      &:after
        background-color: #fff
  .button-close
    &.active
      transform: rotate(45deg)
// Tag Menu Show
.tag-list--menu
  display flex
  justify-content space-between
  align-items center
// New Tag Input
.tag-add--input
  margin-bottom 0
  margin-right 10px
  height 42px


.button
 background-color: #444ce0
 color #fff
 opacity .7
 cursor pointer
 padding 10px 20px
 border-radius 5px
 transition .3s
 &:hover
  opacity 1







</style>


