<template lang="pug">
 .content-wrapper
  section
   .container
     .task-list__header
      h1 Tasks
      .buttons-list
       p {{ filter }}
       .button.button__filter.button-default(
       @click="filter = 'active'") Active
       .button.button__filter.button-default(
       @click="filter = 'completed'") Completed
       .button.button__filter.button-default(
       @click="filter = 'all'") All
     .task-list
      
      .task-item(
         v-for="(task , index) in tasksFilter" :key="task.id" :class="{ completed: task.completed }"
      )
       .card.mycard
        div.mycard__description
         span {{ task.whatWatch }}
         span Total Time:
         span {{ task.time }}
         h2.task__title
          span.task__completed          
           input(type="checkbox" , name="scales" , checked="" , v-model="task.completed")
           |{{ task.title }} 
         p.task__description {{ task.description }}
         
          
         
        div.cross(@click="deleteTask($event, index)")
         span
         span
           // All tags
        .tag-list
         .tag__wrapper(
          v-for="tag in task.tags"
          :key="tag.title"
         )
          span.tag {{ tag.title }}
             
    
</template>


<script>

export default {
 data () {
     return {
         filter: 'active'
     }
 } ,
    
  computed: {
      tasksFilter () {
        if(this.filter === 'active') {
            return this.$store.getters.taskNotCompleted
        } 
        else if(this.filter === 'completed') {
             return this.$store.getters.taskCompleted  
        }
        else if(this.filter === 'all') {
            return this.$store.getters.tasks  
        }
        
        return this.filter === 'active'  
      } ,
      tasks () {
        return  this.$store.getters.tasks
      }
  }
}    


</script>


<style lang="stylus" scoped>
.task__completed 
 display -webkit-flex
 display -moz-flex
 display -ms-flex
 display -o-flex
 display flex
 align-items center

.task__completed input
 margin-right 10px

.task__description 
 margin-top 15px



.tag
 border: 1px solid #000
 border-radius: 5px
 padding: 3px 15px


 


.cross 
  position relative
  
  width: 32px
  height: 32px
  display inline-block
  z-index 5
  cursor pointer
  right -98%
  bottom 105px


.cross span:first-child ,
.cross span:last-child
 
  position: absolute;
  left: 0
  content: ' '
  height: 33px
  width: 2px
  background #424242
 


.cross span:first-child 
  transform: rotate(45deg);

.cross span:last-child 
  transform: rotate(-45deg);

.buttons-list
 display -webkit-flex
 display -moz-flex
 display -ms-flex
 display -o-flex
 display flex
 & .button__filter
    margin 10px
    border 1px solid #000
    border-radius 5px
    padding 3px 15px
    cursor pointer




 




</style>


