import Vue from 'vue'
import Vuex from 'vuex'


import user from './user'
import task from './task'
import tags from './tags'
import user_id from './user_help'

// указываем vue что хотим использовать Vuex
Vue.use(Vuex)

// Создаем глобальный объект Vuex
export default new Vuex.Store ({
    modules: {
        task , tags , user , user_id
    } 
})


